@import 'main';
// End Of Import


.main{
    transition: .5s;

    &.trans{
        transform: translateX(-260px);
    }
}
// Mob Nav

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: rgba($color-prim, 0.3)
    }

    &::-moz-scrollbar {
        width: 8px;
    }
    &::-moz-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
    }
    &::-moz-scrollbar-thumb {
        border-radius: 10px;
        background-color: rgba($color-prim, 0.3)
    }

    &.open{
        right: 0;
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
        width: 100%;
        // overflow: auto;
        // padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 25px;
            width: 100%;

            &.lang{
                display: none;

                @include Sm{
                    display: block;
                }
            }

            &:not(:last-child) {
                border-bottom: 1px solid #f3f3f3;
            }

            &.drpDown{
                background-color: #f2f2f2;
                padding: 0;

                & > a{
                    // margin-bottom: 10px;
                    padding: 7px 25px;
                }
            }

            a {
                width: 100%;
                display: block;
                font-size: 15px;
                color: $color-prim;
                transition: .5s;                  

                &:hover{
                    color: $color-sec;
                }
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                // padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;
                background-color: #fafafa;

                li{
                    padding: 7px 35px;

                    &:not(:last-child){
                        border-bottom: 1px solid #f3f3f3;
                    }

                    a{
                        font-size: 15px;
                        color: #929292;
                        transition: .5s;
                        
                        &:hover{
                            color: #000;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

.up{
    background-color: $color-prim;

    @include Sm{
        display: none;
    }

    .details{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 0;
    }

    .right{

        span{
            color: $color-white;
            font-size: 12px;

            &:not(:last-child){
                margin-left: 40px;
            }

            i{
                font-size: 12px;
                margin-left: 5px;
            }
        }
    }

    .lang{

        a{
            font-size: 12px;
            color: #929292;
            transition: .5s;

            &:hover{
                color: $color-white;
            }
        }
    }
}

// Head

.head{

    .container{
        border-bottom: 1px solid #f2f2f2;
    }

    .content{
        padding: 10px 0 25px;
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 15px;
        cursor: pointer;

        @include Sm{
            display: none;
        }
        
        span{
            width: 100%;
            height: 2px;
            background-color: #000;
            position: relative;
            display: block;

            &:after, &:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: $color-prim;
            }

            &:after{
                top: -8px;
            }
    
            &:before{
                top: 8px;        
            }
        }
    }

    .search{
        position: relative;
        width: max-content;
        margin: 0 40px;
        margin-top: 25px;

        @include Sm{
            order: 3;
        }

        @include Xs{
            margin-top: 10px;
        }

        @include mob480{
            width: 100%;
        }

        input{
            width: 555px;
            height: 55px;
            border-radius: 100px;
            border: 2px solid #f7f7f7;
            padding: 0 35px;
            color: #b6b6b6;
            transition: .5s;

            &::placeholder{
                color: #b6b6b6;
            }

            &:focus{
                border: 2px solid #000;
            }

            @include Md{
                width: 465px;
            }

            @include Lg{
                width: 555px;
            }

            @include Xs{
                width: 435px;
            }

            @include mob480{
                width: 100%;
            }
        }

        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 10px;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: #929292;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;

            &:hover{
                background-color: #000;
            }
        }
    }

    .menu{
        margin-top: 25px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-grow: 1;

        .bars{
            display: none;
            @include Sm{
                display: flex;
                margin-left: 25px;
            }
        }

        & > div{

            // &:hover{
            //     svg{

            //         path{
            //             fill: #000;
            //         }
            //     }
            // }

            svg{

                path{
                    transition: .5s;
                    fill: #000;
                }
            }

            &:not(:last-child){

                svg{
                    margin-left: 25px;
                }
            }

            &.user{
                position: relative;

                &:hover{
                    
                    .drop{
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(0px);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    right: -101%;
                    width: 132px;
                    min-height: 75px;
                    border-radius: 7px;
                    transform: translateY(10px);
                    background-color: rgb(255, 255, 255);
                    text-align: center;                    
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    opacity: 0;
                    visibility: hidden;
                    z-index: 9;
                    transition: .5s;

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }

                    ul{
                        padding: 10px 13px;

                        li{
                            padding: 0 12px;

                            a{
                                color: $color-prim;
                                transition: .5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-sec;
                                }
                            }

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                transition: .5s;
                            }
                        }
                    }
                }
            }

            &.cart{
                position: relative;

                span{
                    position: absolute;
                    top: 50%;
                    right: 50%;
                    transform: translate(45%, -35%);
                    color: $color-prim;
                }
            }
        }
    }
}

// Head

.owl-nav{
    position: absolute;
    top: 49%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 100px;

    @include Sm{
        display: none;
    }

    button{

        &.owl-prev{
            span{
                border-left: 0;
                border-bottom: 0;
            }
        }

        &.owl-next{
            span{
                border-right: 0;
                border-top: 0;
            }
        }

        span{
            display: block;
            width: 15px;
            height: 15px;
            border: 1px solid $color-prim;
            transform: rotate(45deg);
        }
    }
}

// Slider Styles

.owl-dots{
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs{
        bottom: 15px;
    }
    
    button{

        &:not(:last-child){
            margin-left: 5px;
        }

        &.active{

            span{
                background-color: $color-prim;
            }
        }

        span{
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: rgba($color-prim, 0.3);
        }
    }
}

// Slider Styles

// Categories

.categories{
    margin-top: 20px;

    .col-md-3{
        &:last-child{

            @include Xs{
                .category{
                    margin-bottom: 0 !important;
                }
            }
        }
    }

    .category{
        text-align: center;

        @include Md{
            margin-bottom: 25px;
        }

        @include Lg{
            margin-bottom: 0px;
        }

        @include Sm{
            margin-bottom: 25px;
        }

        &:hover{

            .pic{
                img:nth-child(1){
                    transform: scale(1.02);
                }
            }

            .link{
                color: $color-sec;
            }
        }

        .pic{
            background-color: #f7f7f7;
            border-radius: 15px;
            height: 270px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            img:nth-child(1){
                position: relative;
                z-index: 2;
                transition: .5s linear;
            }

            img:nth-child(2){
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%, -50%);
                z-index: 1;
            }
        }

        .link{
            color: $color-prim;
            font-size: 17px;
            margin-top: 15px;

            p{
                margin-bottom: 0;
                transition: .5s;
            }
        }
    }
}

// Categories

// Slider
.disc.custom{

    margin-top: 80px;

    @include Md{
        margin-top: 50px;
    }

    @include Lg{
        margin-top: 80px;
    }

    @include Xs{
        margin-top: 50px;
    }

    .disc-head{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 20px;
    
        & > p{
            font-size: 22px;
            font-family: "frtBold";
            color: #000;
            margin-bottom: 0;
            margin-right: 25px;

            @include mob375{
                font-size: 20px;
            }
        }
    
        & > a{
            font-size: 16px;
            font-family: "frt";
            color: #9f9f9f;
            transition: .5s;

            &:hover{
                color: $color-prim;
            }

            @include Xs{
                margin-left: 25px;
            }
        }
    }
    
    .owl-carousel{

        .owl-item{
            display: flex;
            justify-content: center;
        }

        .owl-nav{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 105%;
            height: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 30px;
            margin-top: 0;
            color: #c4c4c4;
            padding: 0;
            right: unset;

            @include Md{
                width: 102%;                
            }

            @include Lg{
                width: 105%;                
            }

            @include Sm{
                display: none;
            }

            button{
                width: 15px;
                height: 15px;
                border: 2px solid #d0d0d0;
                border-right: none;
                border-bottom: none;
                transform: rotate(-45deg);
                border-radius: 1px;
                transition: .5s;

                &.owl-prev{
                    transform: rotate(135deg);
                }

                &:hover{
                    background-color: transparent;
                    border: 2px solid $color-prim;
                    border-right: none;
                    border-bottom: none;
                }

                span{
                    display: none;
                }
            }
        }

        .owl-dots{
            display: none;

            @include Sm{
                display: flex;
                justify-content: center;
                align-items: center;
                position: unset;
                transform: translateY(35px);
            }
        }

        .owl-item{

            img{
                width: initial;
                margin: auto;
            }
        }
    
        .product{
            position: relative;
            width: 214px;
            text-align: center;
            border-radius: 5px;
            border: 1px solid #e3e3e3;
            background-color: transparent;
            overflow: hidden;
            transition: .5s;

            &:hover{
                background-color: $color-white;

                .details{
                    background-color: $color-white;
                }

                .over{

                    button:first-child, button:last-child{
                        opacity: 1;
                        transform: translateY(0);
                        visibility: visible;
                    }
                }
            }

            .over{
                position: absolute;
                top: 40%;
                transform: translateY(-50%);
                right: 0;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
    
                button{
                    width: 45px;
                    height: 45px;
                    border-radius: 50%;
                    border: 0;
                    background-color: rgba($color-sec, 1);
                    font-size: 18px;
                    color: $color-white;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: .5s;
                    opacity: 0;
                    visibility: hidden;
    
                    &:not(:last-child){
                        margin-left: 5px;
                    }
    
                    &:hover{
                        background-color: rgba($color-prim, 1);
                    }
    
                    &:first-child{
                        transform: translateY(10px);
                    }
    
                    &:last-child{
                        transform: translateY(-10px);
                    }
                }
            }

            .pic{
                padding: 0px 10px;
                height: 215px;
                background-color: $color-white;
                display: flex;
                align-items: center;
                justify-content: center;
            }

            .detail{
                padding: 0 0 20px 0;
                background-color: transparent;
            }

            & > a{
                display: flex;
                align-items: center;
                justify-content: center;
            }
    
            // &:hover{
            //     box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
            // }
    
            & > img{
                width: initial;
                height: 167px;
                display: block;
                margin: auto;
            }
    
            .pro-title{
                font-size: 14px;
                font-family: "frtBold";
                color: #000;
                // margin-top: 0px;
                transition: .5s;

                &:hover{
                    color: $color-sec;
                }
            }
    
            span{
                font-size: 16px;
                font-family: "frtBold";
                display: inline-block;
    
                &.price{
                    color: $color-prim;
                    margin-right: 8px;
                }
    
                &.discount{
                    color: #bbbbbb;
                    text-decoration: line-through;
                    font-family: "frt";
                }
            }
        }
    }
}

