// News

.news{
    padding: 35px 0;
    background-color: $color-prim;

    .row{
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        // @include Md{
        //     flex-direction: column;
        // }

        @include Lg{
            flex-direction: row;
        }

        @include Sm{
            flex-direction: column;
        }
        
        .content{
            display: flex;
            // justify-content: center;
            align-items: center;
            flex-basis: calc((100% / 11) * 4);
            margin-left: 20px;

            @include Sm{
                justify-content: center;
                align-items: center;
                margin-left: 0;
            }

            @include Xs{
                flex-direction: column;
            }

            .icon{
                margin-left: 20px;
                i{
                    font-size: 47px;
                    color: rgb(255, 255, 255);
                    line-height: 0.778;                      
                }

                @include Xs{
                    margin: 0;
                    margin-bottom: 15px;
                }
            }

            .details{

                @include Xs{
                    text-align: center;
                }

                p{
                    font-size: 22px;
                    color: rgb(255, 255, 255);
                    line-height: 1.364;
                    font-family: "frtBlk";
                    margin-bottom: 5px;
                }

                span{
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    line-height: 1.364;
                }
            }
        }

        .form{
            position: relative;
            flex-grow: 1;

            @include Md{
                margin: 25px 0;
            }

            @include Lg{
                margin: 0;
            }

            @include Sm{
                margin: 25px 0 0;
                width: 70%;
            }

            @include mob480{
                width: 90%;
            }

            input{
                background-color: rgb(255, 255, 255);
                width: 100%;
                height: 55px;
                border: 0;
                border-radius: 100px;
                padding: 0 40px;
                font-size: 15px;
                color: rgb(159, 158, 158);
                line-height: 2;
                
                &::placeholder{
                    color: rgb(159, 158, 158);
                }

                @include mob480{
                    width: 100%;
                }
            }

            button{
                width: 40px;
                height: 40px;
                border: 0;
                background-color: transparent;
                font-size: 18px;
                color: #000;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 30px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;

                @include Md{
                    left: 30px;
                }

                @include Lg{
                    left: 30px;
                }

                @include Sm{
                    left: 20px;
                }

                &:hover{
                    color: $color-sec;
                }
            }
        }
    }
}

// News

// footer

footer{
    padding: 58px 0;
    padding-bottom: 0;

    @include Sm{
        padding: 40px 0;
    }

    .foot-links{

        // @include Md{
        //     text-align: center;
        // }

        @include Lg{
            text-align: right;
        }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 18px;
            font-family: "frtBold";
            color: #000;
            margin-bottom: 15px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 15px;
            font-family: "frt";
            color: #000;
            transition: .5s;

            &:hover{
                color: #929292;
            }
        }
    }

    .socialsec{

        @include Md{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: 25px;
        }

        @include Lg{
            align-items: flex-start;
            margin-top: 0;
        }

        @include Sm{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: 25px;
        }

        p{
            font-size: 18px;
            font-family: "frtBold";
            color: #000;
            margin-bottom: 25px;              
        }

        .social{
            display: flex;
            align-items: center;
    
            a{
                display: block;
                width: 45px;
                height: 45px;
                background-color: #f2f2f2;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #929292;
                font-size: 16px;
                transition: .5s;
    
                &:not(:last-child){
                    margin-left: 10px;
                }
    
                &:hover{
                    background-color: #000;
                    color: $color-white;
                }
            }
        }
    }

    .pay{
        margin-top: 35px;
        margin-bottom: 50px;
        display: block;

        @include Sm{
            margin-top: 20px;
            margin-bottom: 0px;
        }

        img{
            margin: auto;
        }
    }
}

// End Of Footer

// End

.end{

    .container{
        border-top: 1px solid #ededed;
        padding: 0;
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0;

        @include mob600{
            flex-direction: column;
        }

        @include Xs{
            text-align: center;
        }


        p,a{
            font-size: 14px;
            color: #929292;
            margin-bottom: 0;

            @include Xs{
                font-size: 13px;
            }
        }

        a{

            @include mob600{
                margin-top: 5px;
            }

            img{
                margin-right: 5px;
            }
        }
    }
}

// End